

import Vue, { PropType } from 'vue';
import ICONS from '@/utils/icons';
import { IIconsTypes } from '@/types/constants';

export default Vue.extend({
  name: 'icon',
  props: {
    iconType: {
      type: String as PropType<IIconsTypes>,
      required: true,
    },
    clickable: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    icon():string {
      return ICONS[this.iconType];
    },
  },
  data() {
    return {
      ICONS,
    };
  },
  methods: {
    onClick() {
      if (this.clickable) {
        this.$emit('click');
      }
    },
  },
});
